
import Cell from 'vux/src/components/cell/index.vue'

export default {
name: 'item',
components: {
Cell
},
props: ['item'],
data () {
return {
msg: 'main',
statusClass: {}
}
},
computed: {
imporcess () {
return this.item.status === 'SHIPMENT_INPROCESS'
},
statusLabel () {
let label = '未出货'
if (this.item.pickUpStatus === 'LACK') {
label = '未出货'
this.statusClass = {
'label-primary': true
}
} else if (this.item.status === 'NOT_SHIPMENT') {
label = '未出货'
this.statusClass = {
'label-primary': true
}
} else if (this.item.status === 'SHIPMENT_INPROCESS') {
label = '出货中'
this.statusClass = {
'label-inprocess': true
}
} else if (this.item.status === 'SHIPMENT_SUCCESS') {
label = '已出货'
this.statusClass = {
'label-info': true
}
} else {

label = '出货失败'
this.statusClass = {
'label-fail': true
}
}
return label
}
}
}



